<template>
  <transition>
    <div class="popup" v-if="show">
      <div class="popup__body">
        <slot></slot>
      </div>

      <div class="popup__overlay" @click="toggle"></div>
    </div>

  </transition>

</template>

<script>
export default {
  name: "Popup",
  props: {
    show: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    toggle() {
      this.$emit('update:show', false)
    }
  }
}
</script>

