<template>
  <div>
    <div class="note-preview" v-if="Object.keys(currentNote).length !== 0">

      <div class="note-preview__date">{{getDate}}</div>





      <editor
          :value="currentNote.html"
          @update="saveChanges"

          :key="currentNote.id"
          class="note-preview__editor"
      ></editor>
      <div class="note-preview__footer">
        <div class="note-preview__nav">

          <img src="@/assets/icons/check_list.svg" alt="" class="note-preview__btn"
               @click="emitter.emit('list')"
          >
          <div class="tooltip">
            <img src="@/assets/icons/letter.svg" @click="showTextEdit = !showTextEdit" alt="" class="nav__btn btn-letter">
            <popup v-model:show="showTextEdit">
              <div class="tooltip__header">
                <img src="@/assets/icons/bold.svg" alt="" class="nav__btn"
                     @click="emitter.emit('bold')"
                >
                <img src="@/assets/icons/italic.svg" alt="" class="nav__btn"
                     @click="emitter.emit('italic')"
                >
                <img src="@/assets/icons/underline.svg" alt="" class="nav__btn"
                     @click="emitter.emit('underline')"
                >
                <img src="@/assets/icons/strokeline.svg" alt="" class="nav__btn"
                     @click="emitter.emit('stroke')"
                >
              </div>
              <div class="tooltip__body">
                <div class="tooltip__option tooltip__option-header1"
                     @click="emitter.emit('header1')"
                >Название</div>
                <div class="tooltip__option tooltip__option-header2"
                     @click="emitter.emit('header2')"
                >Заголовок</div>
                <div class="tooltip__option"
                     @click="emitter.emit('p')"
                >Основной текст</div>
                <div class="tooltip__option option-list" @click="emitter.emit('numlist')">
                  <ol>
                    <li>Нумерованый список</li>
                  </ol>

                </div>
                <div class="tooltip__option option-list" @click="emitter.emit('marklist')">
                  <ul>
                    <li>Маркированый список</li>
                  </ul>

                </div>
              </div>
            </popup>
          </div>
          <div class="tooltip">
            <img src="@/assets/icons/table.svg" @click="showTableEdit = !showTableEdit" alt="" class="note-preview__btn">
            <popup v-model:show="showTableEdit">
              <div class="tooltip-table__header">
                <form action="" @submit.prevent="createTable" class="tooltip-table__form">
                  <div class="tooltip-table__input">
                    <label>Количесво столбцов: </label>
                    <input type="text" v-model="col">
                  </div>
                  <div class="tooltip-table__input">
                    <label>Количесво строк: </label>
                    <input type="text" v-model="row">
                  </div>

                  <button class="tooltip-table__btn">Создать таблицу</button>
                </form>

              </div>

            </popup>
          </div>

        </div>
      </div>


    </div>


    <div class="note-preview_empty" v-else>
      <p>Выберите заметку</p>
    </div>


  </div>




</template>

<script>
import editor from "@/components/Editor";
import {mapActions, mapGetters, mapMutations, mapState} from "vuex";
import tooltip from "@/components/UI/tooltip";
import Popup from "@/components/UI/Popup";

export default {
  name: "NotePreview",
  components: {Popup, editor, tooltip},
  data() {
    return {
      showTextEdit: false,
      showTableEdit: false,
      row: 3,
      col: 3
    }
  },
  methods: {
    ...mapMutations({
      setCurrentNote: 'setCurrentNote',
      setNotes: 'setNotes'
    }),
    ...mapActions({
      setLocalStorage: 'setLocalStorage'
    }),
    saveChanges(noteHTML, noteText) {
      const text = noteText.split('\n').filter(i => i !== '')
      this.setCurrentNote({
        html: noteHTML,
        title: text[0] ? text[0] : 'Новая заметка',
        body: text[1] ? text[1] : 'Нет дополнительного текста',
        text: text.length === 0 ? [''] : text,
        editDate: new Date()
      })

      let notesCopy = [...this.notes]
      notesCopy.forEach(note => {
        if (note.id === this.currentNote.id) {
          note.title = this.currentNote.title
          note.body = this.currentNote.body
          note.text = this.currentNote.text
          note.html = noteHTML
          note.editDate = new Date()
        }
      })
      this.setNotes(notesCopy)
      this.setLocalStorage()
    },
    createTable() {
      this.emitter.emit('table', [this.row, this.col])
    }


  },
  computed: {
    ...mapState({
      currentNote: state => state.currentNote,
      notes: state => state.notes
    }),
    getDate() {
      let createDate = new Date(this.currentNote.createDate)
      let editDate = new Date(this.currentNote.editDate)
      let date = null
      let edited = false
      if (createDate.valueOf() < editDate.valueOf()) {
        date = new Date(editDate)
        edited = true
      } else {
        date = new Date(createDate)
      }

      const fullDate = [
          date.getDate(), date.getMonth(), date.getFullYear()
      ].map(i => (i < 10) ? '0' + i : i).join('.')

      const time = [
          date.getHours(), date.getMinutes()
      ].map(i => (i < 10) ? '0' + i : i).join(':')

      if (edited)
        return 'Изменено: ' + fullDate + ' в ' + time
      else
        return 'Создано: ' + fullDate + ' в ' + time
    }

  },

}
</script>

