<template>

  <editor-content :editor="editor" class="editor"/>
</template>

<script>
import { Editor, EditorContent } from '@tiptap/vue-3'
import Document from '@tiptap/extension-document'
import StarterKit from '@tiptap/starter-kit'
import BulletList from '@tiptap/extension-bullet-list'
import {Paragraph} from "@tiptap/extension-paragraph";
import TaskList from '@tiptap/extension-task-list'
import TaskItem from '@tiptap/extension-task-item'
import Underline from '@tiptap/extension-underline'
import Heading from '@tiptap/extension-heading'
import OrderedList from '@tiptap/extension-ordered-list'
import ListItem from '@tiptap/extension-list-item'
import Table from '@tiptap/extension-table'
import TableRow from '@tiptap/extension-table-row'
import TableCell from '@tiptap/extension-table-cell'
import TableHeader from '@tiptap/extension-table-header'
import Gapcursor from '@tiptap/extension-gapcursor'

const CustomDocument = Document.extend({
  content: 'heading block*',
})

export default {
  name: "Editor",
  components: {
    EditorContent,
  },
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      editor: null
    }
  },



  mounted() {
    this.editor = new Editor({
      extensions: [
        CustomDocument,
        StarterKit.configure({
          document: false,
        }),
        BulletList.configure({
          HTMLAttributes: {
            class: 'list__item',
          },

          itemTypeName: 'listItem',
        }),
        Paragraph,
        Text,
        TaskList,
        TaskItem.configure({
          nested: true,
        }),
        Underline,
        Heading.configure({
          levels: [1, 2, 3],
        }),
        OrderedList,
        ListItem,
        Gapcursor,
        Table.configure({
          resizable: true,
        }),
        TableRow,
        TableHeader,
        TableCell,
      ],
      content: this.value,
      onUpdate: () => {
        this.$emit('update', this.editor.getHTML(), this.editor.getText())
      },

    })

    this.emitter.on('list', (function () {
      this.editor.chain().focus().toggleTaskList().run()
    }).bind(this))

    this.emitter.on('bold', (function () {
      this.editor.chain().focus().toggleBold().run()
    }).bind(this))

    this.emitter.on('italic', (function () {
      this.editor.chain().focus().toggleItalic().run()
    }).bind(this))

    this.emitter.on('stroke', (function () {
      this.editor.chain().focus().toggleStrike().run()
    }).bind(this))

    this.emitter.on('underline', (function () {
      this.editor.chain().focus().toggleUnderline().run()
    }).bind(this))

    this.emitter.on('header1', (function () {
      this.editor.chain().focus().toggleHeading({ level: 1 }).run()
    }).bind(this))

    this.emitter.on('header2', (function () {
      this.editor.chain().focus().toggleHeading({ level: 2 }).run()
    }).bind(this))


    this.emitter.on('p', (function () {
      this.editor.chain().focus().setParagraph().run()
    }).bind(this))

    this.emitter.on('numlist', (function () {
      this.editor.chain().focus().toggleOrderedList().run()
    }).bind(this))

    this.emitter.on('marklist', (function () {
      this.editor.chain().focus().toggleBulletList().run()
    }).bind(this))

    this.emitter.on('table', (function ([row, col]) {
      console.log(row, col)
      this.editor.chain().focus().insertTable({ rows: +row, cols: +col, withHeaderRow: true }).run()
    }).bind(this))







  },

}
</script>

