<template>
  <div>
    <search-bar class="gallery__searchbar"></search-bar>
    <div class="gallery" v-if="searchedNotes.length > 0">
      <transition-group name="note-list">


        <gallery-item
            v-for="note in searchedNotes"
            :key="note.id"
            :note="note"
            class="gallery__item note-list-item"
            @click="openNote(note)"
        ></gallery-item>
      </transition-group>
    </div>

  </div>

</template>

<script>
import GalleryItem from "@/components/galleryItem";
import SearchBar from "@/components/UI/SearchBar";
import {mapGetters, mapMutations} from "vuex";
export default {

  name: "Gallery",
  components: {GalleryItem, SearchBar},
  computed: {
    ...mapGetters({
      searchedNotes: 'searchedNotes'
    })
  },
  methods: {

    openNote(note) {
      this.setCurrentNote(note)
      this.$router.push('/note/' + note.id)
    },
    ...mapMutations({
      setCurrentNote: 'setCurrentNote'
    })
  },


}
</script>

<style  lang="sass">
  .gallery__searchbar
    margin: 10px 0
    width: 100%
    @media screen and (min-width: 700px)
      display: none !important

  .gallery
    padding: 5px
    display: grid
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr))
    @media screen and (max-width: 710px)
      grid-template-columns: repeat(2,1fr)

      width: 100%
    grid-gap: 1em



  .note-list-item
    transition: transform .3s ease


  .note-list-enter-from,
  .note-list-leave-to
    opacity: 0
    transform: translateX(30px)

  .note-list-leave-active
    position: absolute




</style>