<template>
<!--  <icon/>-->
  <div class="nav">
    <div class="nav__body wrapper">
      <div class="nav__left-block nav__block left-block" >
        <div class="nav__icon-group">

          <router-link to="/" class="nav__hide_700">
            <img src="@/assets/icons/menu.svg" alt="" class="nav__btn">
          </router-link>





          <router-link to="/gallery">
            <img src="@/assets/icons/gallery.svg" alt="" class="nav__btn">
          </router-link>
        </div>
        <img src="@/assets/icons/trash.svg" alt="" class="nav__btn"
             @click="removeNote"
        >
      </div>
      <div class="nav__right-block nav__block">
        <img src="@/assets/icons/edit.svg" alt="" class="nav__btn"
             @click="createNewNote"
        >
        <div class="nav__icon-group nav__hide_1100">

          <img src="@/assets/icons/check_list.svg" alt="" class="nav__btn"
            @click="emitter.emit('list')"
          >
          <div class="tooltip">
            <img src="@/assets/icons/letter.svg" @click="showTextEdit = !showTextEdit" alt="" class="nav__btn btn-letter">
            <tooltip v-model:show="showTextEdit">
              <div class="tooltip__header">
                <img src="@/assets/icons/bold.svg" alt="" class="nav__btn"
                     @click="emitter.emit('bold')"
                >
                <img src="@/assets/icons/italic.svg" alt="" class="nav__btn"
                     @click="emitter.emit('italic')"
                >
                <img src="@/assets/icons/underline.svg" alt="" class="nav__btn"
                     @click="emitter.emit('underline')"
                >
                <img src="@/assets/icons/strokeline.svg" alt="" class="nav__btn"
                     @click="emitter.emit('stroke')"
                >
              </div>
              <div class="tooltip__body">
                <div class="tooltip__option tooltip__option-header1"
                     @click="emitter.emit('header1')"
                >Название</div>
                <div class="tooltip__option tooltip__option-header2"
                     @click="emitter.emit('header2')"
                >Заголовок</div>
                <div class="tooltip__option"
                     @click="emitter.emit('p')"
                >Основной текст</div>
                <div class="tooltip__option option-list" @click="emitter.emit('numlist')">
                  <ol>
                    <li>Нумерованый список</li>
                  </ol>

                </div>
                <div class="tooltip__option option-list" @click="emitter.emit('marklist')">
                  <ul>
                    <li>Маркированый список</li>
                  </ul>

                </div>
              </div>
            </tooltip>
          </div>
          <div class="tooltip">
            <img src="@/assets/icons/table.svg" @click="showTableEdit = !showTableEdit" alt="" class="nav__btn">
            <tooltip v-model:show="showTableEdit">
              <div class="tooltip-table__header">
                <form action="" @submit.prevent="createTable" class="tooltip-table__form">
                  <div class="tooltip-table__input">
                    <label>Количесво столбцов: </label>
                    <input type="text" v-model="col">
                  </div>
                  <div class="tooltip-table__input">
                    <label>Количесво строк: </label>
                    <input type="text" v-model="row">
                  </div>

                  <button class="tooltip-table__btn">Создать таблицу</button>
                </form>

              </div>

            </tooltip>
          </div>
          
        </div>
        

        <search-bar class="nav__hide_700 searchbar"></search-bar>
      </div>
    </div>
  </div>

</template>

<script>
import SearchBar from "@/components/UI/SearchBar";
import tooltip from "@/components/UI/tooltip";
import {mapActions, mapMutations, mapState} from "vuex";
// import icon from "@/assets/icons/gallery.svg"


export default {
  name: "NavBar",
  components: {SearchBar, tooltip},
  data() {
    return {
      menuSvg: require('@/assets/icons/menu.svg'),
      showTextEdit: false,
      showTableEdit: false,
      row: 3,
      col: 3
    }
  },

  methods: {
    ...mapMutations({
      setCurrentNote: 'setCurrentNote',
      setNotes: 'setNotes',
      clearCurrentNote: 'setCurrentNote'
    }),
    ...mapActions({
      setLocalStorage: 'setLocalStorage'
  }),

    removeNote() {
      this.setNotes([...this.notes].filter(i => i.id !== this.currentNote.id))
      this.setCurrentNote(
          this.notes[this.notes.length - 1]
      )
      this.setLocalStorage()
    },


    createNewNote() {
      const note = {
        id: (~~(Math.random()*1e8)).toString(16),
        title: 'Новая заметка',
        body: 'Нет дополнительного текста',
        createDate: new Date(),
        editDate: new Date(),
        html: '',
        text: ['']
      }
      this.setCurrentNote(note)
      this.setNotes([...this.notes, note])
      this.setLocalStorage()

    },
    createTable() {
      this.emitter.emit('table', [this.row, this.col])
    }


  },
  computed: {
    ...mapState({
      notes: state => state.notes,
      currentNote: state => state.currentNote
    }),
  }

}
</script>

