<template>
    <div class="tooltip__body" v-if="show">
      <slot></slot>
    </div>
    <div class="tooltip__overlay" @click="toggle" v-if="show">
    </div>

</template>

<script>
export default {
  name: "tooltip",
  props: {
    show: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    toggle() {
      this.$emit('update:show', false)
    }
  }
}
</script>

